// Flexslider

.flexslider {
    margin: 0 !important;
    border: 0 !important;
}

.flex-direction-nav a {
    height: 50px !important;
}

.flex-control-nav {
    display: none;
}

// Form

.btn {
    border-radius: 0;
}

.btn-link {
    &:active, &:hover, &:visited, &:focus {
        outline: 0 !important;
        box-shadow: none !important;
    }
}
.form-control {
    border-radius: 0;
}

// video.js

.video-js {
    width: 100%;
    height: 100%;

    .vjs-tech {
        position: relative !important;
    }

    .vjs-big-play-button {	
        height: 2em !important;
        width: 2em !important;
    	top: 45% !important;
	    left: 45% !important;
        line-height: 1.9em !important;
        border-radius: 1em !important;
    }
}

.vjs-poster {
	background-size: cover !important;
}

.vjs-controls-disabled {
	.vjs-poster {
		display: inline-block;
	}
}

// Modal

.modal-dialog {
    width: 60vw;
}

.modal-header {
    padding: 20px;
    background: #303135;
    position: relative;
    img {
        margin: 0 auto;
        width: 20%;
    }
    .close {
        opacity: 1;
        color: white;
        top: 10px;
        right: 20px;
        margin-top: 0;
        font-size: 36px;
        font-weight: normal;
        position: absolute;
        text-shadow: none;
    }
}